body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 767px) {
    .detailBlock {
        width: 100%;
    }
    .help_cov {
        margin-bottom: 15px;
    }
}

@media (min-width: 768px) {}

@media only screen and (min-device-width: 768px) and (max-device-width: 1023px) {
    .detailBlock {
        width: 60%;
    }
}
/* common_custom */
html {
    height: 100%;
    position: relative;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.375;
}

.no-mar-pad {
    margin: 0 !important;
    padding: 0 !important;
}

.no-mar-padright {
    padding-right: 0;
}

.no-mar-padleft {
    padding-left: 0;
}

.col-centered {
    float: none !important;
    margin: 0 auto !important;
}

a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

p {
    line-height: 1.7;
}

h1, h2, h3, h4 {
    /* font-family: 'Lora', serif; */
    font-family: 'Roboto Slab', serif;
}

/* dash_page  */

#detailWrap {
    background: #3f51b5;
    padding: 50px 0;
}

.profile_photo {
    width: 150px;
    height: 150px;
    margin: 0 auto;
}

.profile_photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.detailBlock {
    /* border: 1px solid #ddd; */
    padding: 30px;
    width: 37%;
    margin: 0 auto;
    box-shadow: 0px 0px 3px #a7a7a7;
    text-align: center;
    background: #fff;
}

ul.info {
    padding: 0;
    margin-bottom: 0;
    border: 1px solid #ddd;
    background: #3c3c3c;
    color: #fff;
}

ul.info li:not(:last-child) {
    border-bottom: 1px solid #ddd;
}

ul.info li {
    list-style: none;
    padding: 15px;
}

.no_people {
    border: 1px solid #ddd;
    padding: 15px;
    margin: 20px;
    background: #00c0f1;
    color: #fff;
}

.help_cov {
    text-align: right;
}

.btn.help {
    background: #00c0f1;
    padding: 12px 20px;
    border-radius: 25px;
    color: #fff;
    font-size: 15px;
    text-transform: uppercase;
}

/* help_page  */

.look {
    background: #FF7F00;
    color: #fff;
    padding: 14px;
    border-radius: 25px;
    width: 90%;
    margin: 0 auto;
    margin-bottom: 15px;
}

.detailBlock .dropdown-toggle {
    width: 100%;
    padding: 16px;
    border-radius: 0;
}

.detailBlock .form-control {
    height: 45px !important;
    margin-bottom: 15px;
}

.btn.sub_help {
    background: #3c3c3c;
    border-radius: 25px;
    padding: 12px 40px;
    color: #fff;
    cursor: pointer;
}

.btn.sub_help:hover, .btn.sub_help:focus {
    background: #545454;
}

.detailBlock b {
    font-size: 18px;
}

.autocomplete-root {
    margin-top: 10px;
}
#mapWrap {
  padding: 40px 0;
}

#mapWrap label {
  font-weight: 700;
}

.mapResult {
  margin-top: 30px;
  text-align: center;
}

.mapResult .form-control {
  margin: 10px 0;
}

.mapResult .btn_sub_help {
  background: #4caf50;
  color: #fff;
  padding: 12px 40px;
  border-radius: 25px;
}

.mapResult .btn_sub_help:hover {
  background: #32a036;
}

.map_title {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 23px;
  border-left: 3px solid #333;
  padding-left: 15px;
}

.mapCov {
  height: 350px;
}

.mapCov .mapCov {
  height: auto;
  margin-bottom: 30px;
}

@media (max-width: 767px) {
  .mapCov {
    height: 300px;
    margin: 0 calc(50% - 50vw);
  }
  .map_title {
    font-size: 20px;
  }
}
@media (max-width: 767px) {
    .detailBlock {
        width: 100% !important;
    }
    .help_cov {
        margin-bottom: 15px;
    }
}

@media (min-width: 768px) {}

@media only screen and (min-device-width: 768px) and (max-device-width: 1023px) {
    .detailBlock {
        width: 60% !important;
    }
}
