/* common_custom */
html {
    height: 100%;
    position: relative;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.375;
}

.no-mar-pad {
    margin: 0 !important;
    padding: 0 !important;
}

.no-mar-padright {
    padding-right: 0;
}

.no-mar-padleft {
    padding-left: 0;
}

.col-centered {
    float: none !important;
    margin: 0 auto !important;
}

a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

p {
    line-height: 1.7;
}

h1, h2, h3, h4 {
    /* font-family: 'Lora', serif; */
    font-family: 'Roboto Slab', serif;
}

.profile_photo {
    width: 150px;
    height: 150px;
    margin: 0 auto;
}

.profile_photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#main #formWrap {
    height: 100vh;
}

.register, .signIn {
    border-radius: 25px;
    padding: 12px 40px;
    color: #fff;
    cursor: pointer;
    width: 55%;
}

.register {
    border: 2px solid #3c3c3c;
    color: #3c3c3c;
    margin-bottom: 20px;
}

.register:hover {
    background: #3c3c3c;
    color: #fff;
}

.signIn {
    background: #3c3c3c;
}

.signIn:hover {
    background: #232323;
    color: #fff;
}

#main #formWrap .formWrap {
    padding: 55px;
}

#formWrap {
    padding: 50px 0;
    display: flex;
    height: calc(100vh - 64px);
    flex-direction: column;
    justify-content: center;
    /* background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #00c0f1), color-stop(100%, #10e281)); */
    background: #3f51b5;
}

#formWrap .formWrap {
    padding: 25px;
    background: #fff;
    border-radius: 0;
    width: 37%;
    margin: 0 auto;
    border: 1px solid #ddd;
}

#formWrap .form-control {
    margin-bottom: 15px;
}

#formWrap .profile_photo {
    width: 130px;
    height: 130px;
    margin-bottom: 15px;
}

#formWrap .form-control {
    height: 45px;
}

#formWrap .cov-rem.clearfix {
    margin-top: 20px;
    font-size: 17px;
}

#formWrap .rem:first-child {
    float: left;
}

#formWrap .rem:last-child {
    float: right;
}

#formWrap hr {
    margin: 6px 0;
    border-color: #909090;
}

#formWrap .small {
    margin: 0;
    font-size: 17px;
}

#formWrap .small a {
    color: #333;
}

#formWrap p.noac {
    text-align: center;
    margin: 0;
    margin-top: 15px;
    font-size: 17px;
}

#formWrap p.noac a {
    color: #3f51b5;
    font-weight: 600;
}

#formWrap .rem input[type="checkbox"] {
    margin-right: 6px;
}

.btn.sub_help {
    background: #3f51b5;
    border-radius: 25px;
    padding: 12px 40px;
    color: #fff;
    cursor: pointer;
}

.btn.sub_help:hover, .btn.sub_help:focus {
    background: #4c63e0;
}

#formWrap h3 {
    text-align: center;
    font-size: 30px;
    margin-bottom: 20px;
    text-transform: capitalize;
    color: #3f51b5;
}

@media (max-width: 767px) {
    #formWrap .formWrap {
        width: 100%;
    }
    .help_cov {
        margin-bottom: 15px;
    }
    .register, .signIn {
        width: 80%;
    }
    #formWrap .small, #formWrap .cov-rem.clearfix {
        font-size: 15px !important;
    }
    #formWrap p.noac {
        font-size: 16px;
    }
}

@media (min-width: 768px) {}

@media only screen and (min-device-width: 768px) and (max-device-width: 1023px) {
    #formWrap .formWrap {
        width: 60%;
    }
    #formWrap {
        height: 100vh;
    }
    .register, .signIn {
        width: 70%;
    }
}

.errorMessage {
    color: red;
    margin-bottom: 10px;
}


.thanks span {
    font-weight: 700;
    font-size: 18px;
    border-bottom: 2px solid #3f51b5;
    padding-bottom: 5px;
}

.thanksWrap p {
    font-weight: 500;
}

.thanksWrap p:nth-last-child() {
    margin-bottom: 0;
}

.suggestion {
    border: 1px solid #ddd;
    padding: 5px 10px;
    margin-bottom: 5px;
}

.form-divider .suggestion {
    width: 65%;
}

.form-divider .suggestion span {
    width: 100%;
}
#messageWrap {
    background: whitesmoke;
    height: calc(100vh - 64px);
    padding: 40px 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.messageCov {
    display: flex;
    background: #fff;
    padding: 30px;
    border: 8px solid #3f51b5;
    justify-content: center;
    flex-direction: column;
}

.messageCov h1 {
    font-size: 2.7rem;
}

.mail {
    position: relative;
    font-size: 25px;
    margin: 15px 0;
}

.mail:before, .mail:after {
    position: absolute;
    width: 10%;
    height: 3px;
    content: '';
    border-top: 1px solid #000;
    margin-top: .60em;
}

.mail:after {
    margin-left: 1%;
}

.mail:before {
    margin-left: -11%;
}

.mail span {
    background: #fff;
    padding: 0 15px;
    color: #4a5fd0;
}

.messageCov p {
    font-size: 1.1rem;
    letter-spacing: 1px;
}

/* additional components  */

.soonCov {
    background: #fff;
    border: 8px solid #3f51b5;
    padding: 60px 30px;
}

.soonCov p {
    font-size: 17px;
    letter-spacing: 1px;
    font-weight: 500;
}

.soonCov .mail span {
    font-weight: 600;
    font-size: 30px;
}

.soonCov .mail:before, .soonCov .mail:after {
    margin-top: 1em;
}

.back {
    color: #3f51b5;
    font-size: 17px;
    font-weight: 600;
}

.back:hover {
    color: #333;
}

@media (min-width: 768px) {
    .messageCov {
        margin: 0 150px;
        min-height: 400px;
    }
}

@media (max-width: 767px) {
    .mail {
        font-size: 18px;
        margin: 8px 0;
    }
    .messageCov h1 {
        font-size: 1.6rem;
    }
    .soonCov {
        padding: 30px 15px;
    }
    .soonCov .mail span {
        font-size: 23px;
    }
    .soonCov p {
        font-size: 16px;
    }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1023px) {
    .messageCov {
        margin: 0 90px;
        min-height: auto;
    }
    .messageCov h1 {
        font-size: 2rem;
    }
}
